.page-body {
  margin: 0;
  padding: 0;
}

.page {
  margin: 0;
  padding: 0;
  .font-arial
}
.section__title {
  .font-oswald;
  font-size: 36px;
  line-height: 40px;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

